import { NextResponse } from "next/server";

export async function GET() {
  // Mock data; replace later with Supabase feed
  const data = [
    { country: "United States", code: "US", score: 85, sentiment: "negative" },
    { country: "China", code: "CN", score: 70, sentiment: "neutral" },
    { country: "Germany", code: "DE", score: 55, sentiment: "neutral" },
    { country: "Japan", code: "JP", score: 40, sentiment: "positive" },
    { country: "India", code: "IN", score: 30, sentiment: "positive" },
    { country: "Brazil", code: "BR", score: 25, sentiment: "neutral" },
    { country: "Russia", code: "RU", score: 90, sentiment: "negative" },
    { country: "France", code: "FR", score: 45, sentiment: "neutral" },
  ];

  return NextResponse.json({ items: data.sort((a, b) => b.score - a.score) });
}
